<style lang="scss">
.active {
  background-color: #ebebeb;
  transition: background-color 0.3s ease;
  & button {
    color: #000;
  }
}
.converter-container {
  position: relative;
  border: 1px solid #dfdfdf;
  border-radius: 5px;
  background-color: #fff;
  width: 100%;
  // box-shadow: 4px 4px 8px 0px rgba(0, 0, 0, 0.08);
  // box-shadow: 0px 0px 8px 0px rgba(0, 0, 0, 0.08);
  // box-shadow: 0px 0px 8px 0px rgba(0, 0, 0, 0.19);
}
.funds {
  margin-bottom: 70px;
  &__title {
    color: #333;
    margin-top: 20px;
    font-size: 25px;
    font-weight: 700;
  }
  &__img {
    width: 100%;
    max-width: 150px;
    height: auto;
  }
  .info {
    font-size: 16px;
    line-height: 20px;
    text-align: justify;
    color: #333;
    &__state {
      z-index: 0;
      padding: 10px;
      background-color: #fff;
      border: 1px solid #dddddd;
      cursor: default;
      transition: all 0.3s ease;
      &:hover {
        box-shadow: 4px 4px 10px 0px rgba(124, 124, 124, 0.2);
        transform: scale(1.02);
      }
    }
  }
  &__values {
    width: 100%;
    overflow: hidden;
    justify-content: space-around;
    height: 200px;
    position: relative;
    hr {
      height: 1px;
      width: 100%;
      border: none;
      border-bottom: 1px solid #dfdfdf;
    }
  }
  &__currency-note {
    padding: 0px 10px 10px 10px;
    font-size: 12px;
    min-height: 22px;
  }
  &__input {
    border: none;
    outline: none;
    width: 100%;
    padding: 10px;
    font-size: 35px;
    &:first-child {
      // border-bottom: 1px solid #dfdfdf;
    }
  }
  &__valutes {
    justify-content: space-between;
    &--bot {
      border-top: 1px solid #dfdfdf;
    }
    &--top {
      border-bottom: 1px solid #dfdfdf;
    }
  }
  &__valute-button-container {
    border-left: 1px solid #dfdfdf;
    width: 100%;
    display: flex;
    justify-content: center;
    transition: background-color 0.2s ease;
    cursor: pointer;
    &:hover {
      background-color: #f0f0f0;
      transition: background-color 0.3s ease;
    }
    &:first-child {
      border-left: 0px;
    }
  }
  &__valute-button {
    padding: 10px;
    color: #333;
    &--more-down {
      &::before {
        transform: rotate(45deg);
      }
      &::after {
        transform: rotate(-45deg);
      }
    }
    &--more-up {
      &::before {
        transform: rotate(-45deg);
      }
      &::after {
        transform: rotate(45deg);
      }
    }
    &--more-down,
    &--more-up {
      position: relative;
      &::before {
        content: "";
        display: block;
        position: absolute;
        width: 15px;
        height: 2px;
        background-color: #333;
        top: 17px;
        left: -5px;
      }
      &::after {
        content: "";
        display: block;
        position: absolute;
        width: 15px;
        height: 2px;
        background-color: #333;
        top: 17px;
        left: 5px;
      }
    }
  }
}
.form-item {
  gap: 25px;
  label {
    width: 50%;
  }
  input,
  p,
  select,
  textarea {
    border: 1px solid #dfdfdf;
    outline: none;
    padding: 10px;
    border-radius: 5px;
    background: #fff;
  }
  *:disabled,
  p {
    background: #ececec;
    color: #545454;
  }
}
.address-block {
  border: 1px solid #dfdfdf;
  border-radius: 30px;
  span {
    border-left: 1px solid #dfdfdf;
    height: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0px 10px;
    img {
      width: 20px;
      height: auto;
      cursor: pointer;
    }
  }
  span:first-child {
    border: none;
  }
}
@media screen and (max-width: 767px) {
  .address-block {
    border-radius: 15px;
    flex-direction: column;
    span {
      border-left: 0;
      border-top: 1px solid #dfdfdf;
      width: 100%;
    }
  }
  .network-block {
    flex-direction: column;
  }
  .form-item {
    label {
      padding-left: 10px;
    }
    flex-direction: column;
    gap: 5px;
    justify-content: flex-start;
    align-items: unset;
  }
  .qr-img {
    width: 200px;
    height: auto;
  }
}
@media screen and (min-width: 768px) {
  .network-block {
    max-width: 850px;
  }
  .form-item {
    gap: 25px;
    label {
      width: 50%;
    }
    input,
    p,
    select,
    textarea {
      width: 100%;
    }
  }
  .qr-img {
    width: 150px;
    height: auto;
  }
}
</style>
